body {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.simple-page-scroller{
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  /* margin-top: 50px; */
  overflow-y: auto;
  height: 100vh;
}

.draggable-item{
  height: 50px;
  line-height: 50px;
  text-align: center;
  width : 100%;
  display: block;
  background-color: burlywood;
  outline: 0;
  border: 1px solid rgba(0,0,0,.125);
  margin-bottom: 2px;
  margin-top: 2px;
  user-select: none;
}

.opacity-ghost {
  transition: all .18s ease;
  background-color: cornflowerblue;
  box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2),
  0px 8px 10px 1px rgba(0, 0, 0, 0.14),
  0px 3px 14px 2px rgba(0, 0, 0, 0.12);
  cursor: ns-resize;
}
